import { Pipe, PipeTransform } from '@angular/core';

let emojiDictionary = [
    { patterns: [':)', ':-)', '=)'], unicode: 'üòÉ' },
    { patterns: [':D', ':-D', '=D'], unicode: 'üòÄ' },
    { patterns: [':(', ':-(', '=('], unicode: 'üôÅ' },
    { patterns: [':|', ':-|', '=|'], unicode: 'üòê' },
    { patterns: [':*', ':-*', '=*'], unicode: 'üòô' },
    { patterns: ['T_T', 'T.T'], unicode: 'üò≠' },
    { patterns: [':O', ':-O', '=O', ':o', ':-o', '=o'], unicode: 'üòÆ' },
    { patterns: [':P', ':-P', '=P', ':p', ':-p', '=p'], unicode: 'üòã' },
    { patterns: ['>.<'], unicode: 'üò£' },
    { patterns: ['@.@'], unicode: 'üòµ' },
    { patterns: ['*.*'], unicode: 'üòç' },
    { patterns: ['<3'], unicode: '‚ù§Ô∏è' },
    { patterns: ['^.^'], unicode: 'üòä' },
    { patterns: [':+1'], unicode: 'üëç' },
    { patterns: [':-1'], unicode: 'üëé' }
];

/*
 * Transforms common emoji text to UTF encoded emojis
*/
@Pipe({name: 'emojify'})
export class EmojifyPipe implements PipeTransform {
    transform(message: string, pipeEnabled: boolean): string {
        if (pipeEnabled && message && message.length > 1) {  
            emojiDictionary.forEach(emoji => {
                emoji.patterns.forEach(pattern => {
                    message = message.replace(pattern, emoji.unicode);
                })
            });
        }

    return message;
  }
}
